header {
  padding-bottom:10px;
  border-bottom:$card-border;
  width:100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height:70px;

  .logo {
    font-size:38px;
    font-weight:bold;
    color:$primary-purple;
  }

  a {
    background-color: $light-gray;
    padding: 10px 30px;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 500;
    box-shadow: inset 0 0 3px 3px #0000002b;
    border-radius: 4px;
    transition:all .2s;

    &:hover {
      box-shadow: none;
      transition:all .2s;
    }
  }
}
