.area-card {
  @include card();
  width:49%;
  margin-right:2%;
  text-align:left;
  display:flex;
  flex-flow:column;
  justify-content: space-between;
  margin-bottom:30px;
  border-radius: 4px;
  overflow:hidden;


  .info {
    padding:30px 20px;

    h4 {
      font-size:28px;
      margin-bottom:10px;
    }

    p {
      font-size:16px;
      line-height:28px;
    }

  }

  a {
    display: inline-block;
    margin:20px 0px;
    width: unset;
    align-self: flex-start;
    margin-left: 20px;
  }

  &:nth-child(2n+2) {
    margin-right:0;
  }
}
